___CSS_LOADER_EXPORT___.push([module.id, `body{
  display: grid;
  grid-template-columns: auto 1fr;
  grid-template-rows: auto 1fr;
  box-sizing: border-box;
  height: 100vh;
  margin: 0;
  padding: 1rem;
}

header{
  grid-column: span 2;
  font-size: 3rem;
  text-align: center;
  border: 2px solid black;
  padding: 1rem;
}

section.nav-bar{
  display: grid;
  width: 300px;
  padding: 2rem;
  box-sizing: border-box;
  border: 2px solid black;
}

div#nav-bar-wrapper{
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

div#content-wrapper{
  max-width: 800px;
  margin: 1rem auto;
  display: grid;
  padding: 0 1rem;
}

/* project list  */

ul.project-list{
  list-style: none;
  padding: 0;
  margin-left: 1rem;
}

ul.project-list li{
  display: flex;
  justify-content: space-between;
  margin: 0;
}

.project.select.button{
  flex-grow: 1;
  /* background-color: white; */
  /* border: none; */
  text-align: left;
  font-family: inherit;
  font-size: 1rem;
  padding: 0;
} 



/* dialog forms */

fieldset{
  width: 300px;
  display: grid;
  grid-template-columns: auto auto;
  gap: 1rem;
}
legend + p{
  grid-column: span 2;
  margin: 0;
}

fieldset button{
  grid-column: span 2;
}

/* tasks */

ul.task-list{
  padding: 0;

}

ul.task-list li{
  list-style: none;
  display: flex;
  margin: 1rem 0;
  gap: 1rem;
  border-left: 3px solid;
  border-radius: 3px;
  height: 2rem;
  align-items: center;
}

ul.task-list li[prio=low]{
  border-color: blue;
}

ul.task-list li[prio=medium]{
  border-color: yellow;
}

ul.task-list li[prio=high]{
  border-color: red;
}

li p{
  margin: 0;
}

li p.task.title{
  flex-grow: 1;
  margin-left: 1rem;
}
button.task.expand{
  margin-left: 1rem;
